				"html": "<h1>Java while Loop</h1>\n<p>The <code>while</code> loop executes a group of statements / single statement till a condition evaluates to <code>true</code>. For instance:</p>\n<pre><code> while(some_condition_is_true){\n  //do something\n }\n</code></pre>\n<p><code>Note</code>: For the <code>while</code> loop to start executing, you'd require the condition to be true. However, to exit the loop you must do something as given below (otherwise the loop will execute forever. Practically, it will run till the JVM runs out of memory).</p>\n<pre><code>while(i&#x3C;10){\n    System.out.println(\"i :\"+ i);\n    i++; //&#x3C;- This ensures that value of i in the while condition will become more than 10 at some point thereby breaking the condition and exiting the loop.\n}\n</code></pre>\n<p>Can you now guess the output of the following snippet?</p>\n<pre><code> int i = 0;\n while( i &#x3C; 10 ){\n  System.out.println(\"Value of i is : \" + i);\n  i++;\n }\n</code></pre>",
